/* General Reset and Body Styling */
/* CSS Reset for body and header to ensure no extra margins or paddings */
body,
html {
  margin: 0;
  padding: 0;
  width: 100%;
  overflow-x: hidden;
}

#root,
.App {
  margin: 0;
  padding: 0;
  width: 100%;
  background-color: #DDDDD5;
}



/* Header Styling */
.header {
  width: 100%;
  position: relative;

  /* Ensure padding/margin don't affect width */
}

/* Header Image Styling */
.headerimg {
  width: 100%;
  height: 50vh; /* Adjust height as needed */
  object-fit: cover;
  display: block; /* Remove inline-block whitespace issues */
}

/* Navigation Bar Styling */
.nav-bar {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 30px 6%;
  z-index: 2;
  position: absolute;
  top: 70%;
  width: 100%;
  box-sizing: border-box; /* Ensure padding/margin don't affect width */
}
.knoopnav{
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 30px 6%;

  position: relative;
  
  width: 100%;
  box-sizing: border-box; /* Ensure padding/margin don't affect width */
}
.logo img {
  max-width: 35vh; /* Adjust the max-width as needed */
  height: auto; /* Maintain aspect ratio */
  display: block; /* Ensure the image doesn’t have inline spacing issues */
  filter: url(#drop-shadow);
  
}
/* Logo Styling */
.logo {
  position: absolute;
  
  top: 40%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.logosgv img {
  max-width: 17vh; /* Adjust the max-width as needed */
  height: auto; /* Maintain aspect ratio */
  display: block; /* Ensure the image doesn’t have inline spacing issues */
  filter: url(#drop-shadow);
  
}
/* Logo Styling */
.logosgv {
  position: absolute;
  
  top: 40%;
  left: 90%;
  transform: translate(-50%, -50%);
}
/* Navigation List Styling */
.nav {
  display: flex;
 
  list-style: none;
  padding: 20px;
  margin: 0;
  background-color: #8c8c73;
  border-radius: 100px;
  align-content: center;
}

.nav li {
  display: inline-block;
 
  padding: 0 20px;
}


.nav a {
  text-decoration: none;
  color: #262626;
  font-size: 1.5em;
  font-family: text;
  transition: color 0.3s ease;
}

.nav a:hover {
  color: #8c8c73;
}

/* Responsive Styles */
.MasterGrid{
  --gap: 6vw;
  --num-cols: 4;
  --row-height: 22vw;
width: 100vw;
  box-sizing: border-box;
  padding-bottom:  var(--gap);
  padding-top:  var(--gap);
  padding-left:  var(--gap);
  padding-right:  var(--gap);

  display: grid;
  grid-template-columns: repeat(var(--num-cols), 1fr);
  grid-auto-rows: var(--row-height);

  gap: var(--gap);
}
.MasterGrid>div {
  height: 100%;
  width: 100%;
  object-fit: cover;
  position: relative;
  overflow: hidden;
  
}
.widgetGrid{
  --gap: 3vw;
  --num-cols: 3;
  --row-height: 20.5vw;
width: 100%;
  box-sizing: border-box;

  padding-top:  var(--gap);
  padding-left:  var(--gap);
  padding-right:  var(--gap);
  padding-bottom:  var(--gap);
  background-color: #8c8c73;
  

  display: grid;
  grid-template-columns: repeat(var(--num-cols), 1fr);
  grid-auto-rows: var(--row-height);

  gap: var(--gap);
}
.widgetGrid>div {
  height: 100%;
  width: 100%;
  object-fit: cover;
  position: relative;
  overflow: hidden;
  background-color: aliceblue;
  border-radius: 10px;
}
.widgetGrid>div img {
  height: 40%;
  width: 100%;
  object-fit: cover;
  position: relative;
  overflow: hidden;
  background-color: aliceblue;
}
.eventGrid{
  --gap: 0.5vw;
  --num-cols: 3;
  --row-height: 10.5vw;
width: 100%;
  box-sizing: border-box;

  padding-top:  var(--gap);
  padding-left:  var(--gap);
  padding-right:  var(--gap);
  padding-bottom:  var(--gap);
  
  

  display: grid;
  grid-template-columns: repeat(var(--num-cols), 1fr);
  grid-auto-rows: var(--row-height);

  gap: var(--gap);
}
.grid-col-4 {
  grid-column: span 4;
}
.grid-row-4 {
  grid-row: span 4;
}
.grid-col-2 {
  grid-column: span 2;
}
.grid-row-2 {
  grid-row: span 2;
}
.grid-col-3 {
  grid-column: span 3;
}
.grid-row-3 {
  grid-row: span 3;
}
